/*===== VARIABLES SCSS =====*/
/*===== VARIABLES CSS =====*/
/*=== Cores ===*/
:root {
  --first-color: #0396a6;
  --dark-color: #000;
  --white-color: #fefdfc;
  --background-white-color: #fefdfc;
  --background-dark-color: #000000;
  --background-green-color: #cdcdc5;
  --text-gay-color: #525560;
  --test-red-color: red;
}

/*=== Fuente y tipografia ===*/
:root {
  --body-font: 'Roboto', sans-serif;
  --biggest-font-size: 4rem;
  --h2-font-size: 3rem;
  --normal-font-size: 1rem;
}

@media screen and (min-width: 768px) {
  :root {
    --biggest-font-size: 4rem;
    --h2-font-size: 2.5rem;
    --normal-font-size: 1rem;
  }
}

/*===== SECTION ===== */
.lSectionCardHighlightsAndPreviousSermons {
  background-color: var(--white-color);
}
.sectionCardHighlightsAndPreviousSermons {
  height: max(100vh, fit-content);
  max-width: 1279px;
  margin-left: 0;
  margin-right: 0;
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.CardContainerCardHighlightsAndPreviousSermons {
  padding: 1rem;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  display: flex; 
  flex-direction: column; 
  align-items: center;
  box-sizing: border-box;
}
.cardCardHighlightsAndPreviousSermons {
  width: 100%;
  padding: 16px;
  box-sizing: border-box;
}
.imageWrapperCardHighlightsAndPreviousSermons {
  position: relative;
  width: 100%;
  max-width: 350px;
  height: 243px;
  overflow: hidden;
  border-radius: 20px;
}
.imageCardHighlightsAndPreviousSermons {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  transition: all 0.3s;
}
.imageBlurCardHighlightsAndPreviousSermons {
  filter: blur(4px);
}
.carouselOverlayCardHighlightsAndPreviousSermons {
  position: absolute;
  inset: 0;
  display: flex;
  flex-direction: column;
  justify-content: end;
  padding: 16px;
  transition: opacity 0.3s;
  z-index: 2;
}
.titleCardHighlightsAndPreviousSermons {
  color: var(--text-white-color);
  font-size: var(--h3-font-size);
  font-weight: bold;
  text-align: center;
}
.descriptionCardHighlightsAndPreviousSermons {
  color: var(--text-white-color);
  margin-top: 8px;
}
.buttonCardHighlightsAndPreviousSermons {
  margin-top: 16px;
  background-color: var(--white-color);
  color: var(--dark-color);
  padding: 8px 16px;
  border-radius: 4px;
  opacity: 0;
  transition: opacity 0.3s;
}
.buttonVisibleCardHighlightsAndPreviousSermons {
  opacity: 1;
}
.buttonVisibleCardHighlightsAndPreviousSermons:hover {
  color: var(--white-color);
  background-color: var(--background-dark-green-color);
  transition: color 0.3s ease;
}

@media screen and (min-width: 760px) {
  .sectionCardHighlightsAndPreviousSermons {
    justify-content: center;
    flex-direction: row;
    margin-left: auto;
    margin-right: auto;
  }
  .CardContainerCardHighlightsAndPreviousSermons {
    max-width: 700px;
    display: flex;
    flex-direction: column; 
  }
  .imageWrapperCardHighlightsAndPreviousSermons {
    max-width: 600px;
  }
  .cardCardHighlightsAndPreviousSermons {
    width: 600px;
  }
}

@media screen and (min-width: 1200px) {
  .sectionCardHighlightsAndPreviousSermons {
    justify-content: space-between;
    flex-direction: row;
    height: 100%;
    min-height: 200px;
  }
  .descriptionCardHighlightsAndPreviousSermons {
    color: var(--text-white-color);
    margin-top: 8px;
  }
  .CardContainerCardHighlightsAndPreviousSermons {
    display: flex;
    max-width: 1280px;
    flex-direction: row; 
  }
  .imageWrapperCardHighlightsAndPreviousSermons {
    max-width: 409px;
  }
  .cardCardHighlightsAndPreviousSermons {
    width: 409px;
  }
  .titleCardHighlightsAndPreviousSermons {
    font-size: var(--h2-font-size);
  }
}
